// fonts
$font-pr: 'Roboto',
sans-serif;
$font-sec: 'HelveticaNeueCyr',
sans-serif;

// colors
$clr-white: #fff;

$clr-bg-pr: #f1f3f2;
$clr-bg-sec: $clr-white;

$clr-bg-side-hdr: #2f3b4c;
$clr-bg-side-body: #e1e5e8;
$clr-bg-side-ftr: #2575aa;
$clr-bg-side-run-pr: #d5d9db;
$clr-bg-side-run-sec: #c1c4c5;

$clr-icon: #afb0c3;

$clr-btn-pr: #12a9eb;
$clr-btn-sec: $clr-white;
$clr-btn-br: #11a9ec;

$clr-txt-pr: #727272;
$clr-txt-sec: #2f3b4c;
$clr-txt-event: #2574a9;

$clr-br-hdr: #e1e7f0;
$clr-br-side: #cfd7dc;
$clr-br-side-s: #bdc9d1;
$clr-br-pr: #bfc2c4;
$clr-br-chart-lg: #ebf2f4;

$clr-status-on: #14741b;
$clr-status-off: #8c9092;

$clr-bg-inventory-card-1chld: #1a9022;
$clr-bg-inventory-card-2chld: #f5ad3c;

$clr-bg-chart-1chld: #1ba39c;
$clr-bg-chart-2chld: #913d88;
$clr-bg-chart-3chld: #f5ad3c;

// box-shadows
$bs-hdr: 1px 2px 2px rgba(4, 4, 4, .09);
$bs-inventory: inset 0 3px 7px rgba(0, 0, 0, .35);
$bs-inventory-btn: 1px 1px 2px rgba(0, 0, 0, .3);


// frequently repeated values
$height: rem(50px);
